<template>
  <!-- loginForm 登入表單 -->
  <a-form layout="inline" v-on:submit="HandleSubmit" v-bind:form="form">
    <div>
      <div style="height: 60px;">
        <a-form-item
          v-bind:validateStatus="UserNameError() ? 'error' : ''"
          v-bind:help="UserNameError() || ''"
        >
          <a-input
            placeholder="帳號"
            v-decorator="[
        'userName',
        {rules: [{ required: true, message: '請輸入帳號' }]}
      ]"
          >
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)"/>
          </a-input>
        </a-form-item>
      </div>
      <!--輸入帳號-->
      <div style="height: 60px;">
        <a-form-item
          v-bind:validateStatus="PasswordError() ? 'error' : ''"
          v-bind:help="PasswordError() || ''"
        >
          <a-input
            v-decorator="[
        'password',
        {rules: [{ required: true, message: '請輸入密碼!' }]}
      ]"
            type="password"
            placeholder="密碼"
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
          </a-input>
        </a-form-item>
      </div>
      <!--輸入密碼-->
      <div>
        <a-form-item>
          <a-button type="primary" htmlType="submit" v-bind:disabled="disabled">Log in</a-button>
        </a-form-item>
      </div>
      <!--登入按鈕-->
      <div>
        <!-- <rcs-alert ref="alert"></rcs-alert> -->
      </div>
      <!--訊息顯示-->
    </div>
  </a-form>
</template>
<script>
import data from '@/T4JSON/IUserInfoBasic.json'
import { apiHost } from '@/APIconfig.js'
export default {
  name: 'rcs-login',
  data () {
    return {
      disabled: false,
      form: this.$form.createForm(this),
      model: data
    }
  },
  methods: {
    UserNameError () {
      return (
        this.form.isFieldTouched('userName') &&
        this.form.getFieldError('userName')
      )
    },
    PasswordError () {
      return (
        this.form.isFieldTouched('password') &&
        this.form.getFieldError('password')
      )
    },
    HandleSubmit (e) {
      e.preventDefault()
      this.disabled = true
      var _self = this
      this.form.validateFields(function (err, values) {
        if (!err) {
          _self.Login(values)
        } else {
          _self.ShowAlert('請輸入帳號密碼!', 'warning')
        }
      })
    },
    Login (model) {
      var vueThis = this
      console.log(apiHost + '/api/Login')
      this.$http
        .get(apiHost + '/api/Login', {
          pData: model
        })
        .then(
          function (response) {
            // debugger
            console.log(response)
            // // response is ok...
            // if (response.data.hasUser) {
            //   // this.ShowAlert('準備登入!', 'success')
            //   vueThis.disabled = false
            //   // window.location.href = this.$http.options.root + '/api/Login/Get'
            // } else {
            //   var noData = !!response.data.msg
            //   if (noData) {
            //     vueThis.ShowAlert(response.data.msg)
            //   }
            //   vueThis.disabled = false
            // }
          },
          function (response) {
            // response is not ok...
            // this.$refs.alert.ShowError()
            vueThis.disabled = false
            console.log(response)
          }
        )
    },
    ShowAlert (pMsg, pType) {
      this.$refs.alert.show(pMsg, pType)
      this.disabled = false
    }
  }
}
</script>
<style>
</style>



